
.view {
  -webkit-transition: all 0.0s ease;
  transition: all 0.0s ease;
}
.test-enter, .test-leave {
  opacity: 0;
  -webkit-transform: translate3d(10px, 0, 0);
          transform: translate3d(10px, 0, 0);
}

.title{
  font-size: +100%;
  font-weight: bold;
}

.test-title div {
  height: 34px;
  line-height: 34px;
  font-size: 120%;
  /*border: 1px solid black;*/
  /*margin: 0 -10px;*/
  padding-left: 5px;
  padding-right: 0px;
}

.emoji {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  line-height: 1.5em;
  text-align: center;
  font-size: 0.8em;
  border: 1px solid gray;
  background: gray;
  border-radius: 1em;
  opacity: 0.8;
}

input:focus,
select:focus,
textarea:focus,
a:focus,
button:focus {
  outline: 0!important;
}



